* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
    color: #222;
    display: block;
    width: 100%;
}

img {
    display: block;
    width: 100%;
}

.title {
    font: {
        size: 28px;
        weight: bold;
    }
    text-align: center;
}

@mixin size($width, $height) {
    width: $width;
    height: $height;
}

@mixin flex_style {
    display: flex;
    align-items: center;
}

@mixin line($height) {
    line-height: $height;
    text-align: center;
}
$color: #fd8234;

/* -------------header--------------- */
.header {
    @include size(100%, 80px);
    background-color: transparent;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;

    font: {
        size: 15px;
    }

    .header_inner {
        @include size(100%, 80px);
        @include flex_style();
        padding: 0 25px;
        justify-content: space-between;

        .header_left {
            position: relative;
            @include flex_style();

            .logo {
                width: 52px;
                margin-right: 35px;
            }
            .lnb {
                ul.menu_list {
                    @include flex_style();

                    li {
                        margin-right: 35px;

                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }
            }
        }

        .header_right {
            @include flex_style();

            ul {
                @include flex_style();

                li {
                    margin-right: 35px;

                    &:last-child {
                        margin-right: 0;
                    }

                    &.search_icon {
                        position: relative;
                        @include size(160px, 25px);
                        border-radius: 20px;
                        background-color: $color;

                        input {
                            background-color: $color;
                            border: none;
                            outline: none;
                            @include size(130px, 25px);
                            border-radius: 20px;
                            padding-left: 15px;
                        }

                        button {
                            position: absolute;
                            top: 5px;
                            right: 7px;
                            background-color: $color;
                            border: none;
                            outline: none;
                            border-radius: 20px;
                            cursor: pointer;
                            color: #fff;
                        }
                    }
                }
            }
        }
    }
}

.lnb_nav {
    position: fixed;
    top: 80px;
    left: -435px;
    background-color: #fff;
    height: calc(100vh - 80px);
    color: #261c13;
    z-index: 1000;
    font: {
        size: 14px;
        weight: 300;
    }

    li {
        &:hover {
            background-color: #efe8da;
            transition: all 0.3s;
        }
    }

    .top_lnb {
        height: 60px;
        line-height: 60px;

        ul {
            @include flex_style();

            li {
                width: 50%;
                padding: 0 20px;
                border-bottom: 1px solid #eee;
            }
        }
    }
    .bottom_lnb {
        li {
            height: 60px;
            width: 435px;
            padding: 0 20px;
            line-height: 60px;
            border-bottom: 1px solid #eee;
        }
    }
}

/* -------------main_visual--------------- */
.main_visual {
    max-width: 100%;
    width: 100%;
    height: 100vh;
    background-color: #eee;

    img {
        width: 100%;
        height: 100vh;
    }
}

/* -------------new_arrivals--------------- */
.new_arrivals {
    @include size(100%, 600px);
    background-color: #f8f8f8;
    border-bottom: 1px solid #ccc;

    p.title {
        padding: 75px 0 60px;
    }

    ul {
        display: flex;
        justify-content: space-evenly;

        li {
            position: relative;
            @include size(360px, 360px);
            border: 1px solid #ccc;

            .txt_box {
                @include size(100%, 75px);
                position: absolute;
                bottom: 0;
                left: 0;
                background-color: rgba(255, 255, 255, 0.6);
                padding: 15px 15px;

                p {
                    &:first-child {
                        font-size: 12px;
                        margin-bottom: 10px;
                    }
                    &:last-child {
                        font: {
                            size: 13px;
                            weight: bold;
                        }
                    }
                }
            }
        }
    }
}

/* -------------best--------------- */
.best {
    @include size(100%, 650px);
    background-color: #f8f8f8;
    border-bottom: 1px solid #ccc;

    p.title {
        padding: 75px 0 20px;
    }

    .slide_list {
        @include flex_style();
        justify-content: space-evenly;

        .box {
            position: relative;
            @include size(340px, 340px);
            border: 1px solid #ccc;
            overflow: hidden;

            &:nth-child(2) {
                @include size(500px, 458px);
            }

            .txt_box {
                @include size(100%, 75px);
                position: absolute;
                bottom: -75px;
                left: 0;
                background-color: rgba(255, 255, 255, 0.6);
                padding: 15px 15px;

                p {
                    &:first-child {
                        font-size: 12px;
                        margin-bottom: 10px;
                    }
                    &:last-child {
                        font: {
                            size: 13px;
                            weight: bold;
                        }
                    }
                }
            }
        }
        .box:hover .txt_box {
            bottom: 0;
            transition: all 0.5s;
        }
    }
}

